<template>
    <div class="material-form">
        <v-container>
            <v-card class="mx-auto elevation-4" max-width="600" outlined>
                <v-card-title class="text-h1 text-center">PO Material</v-card-title>
                <v-divider></v-divider>

                <v-card-text>
                    <!-- Lokasi -->
                    <div class="section">
                        <v-select v-model="selectedLocation"
                            :items="Object.entries(location).map(([id, name]) => ({ id, name }))" item-value="id"
                            item-text="name" label="Pilih Lokasi" outlined dense class="location-select"
                            @change="handleLocationChange"></v-select>
                    </div>

                    <!-- Nama Material -->
                    <div class="section">
                        <v-btn @click="showItemPicker = true" color="primary" text-color="blue darken-3"
                            class="mb-3 btn-custom" outlined>
                            <v-icon left>mdi-plus-circle</v-icon> Tambah item
                        </v-btn>

                        <!-- Dialog untuk Memilih Material -->
                        <v-dialog v-model="showItemPicker" max-width="500" transition="dialog-bottom-transition">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Item</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="itemSearch" placeholder="Cari Item..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(material, index) in filteredItemList" :key="index"
                                            @click="handleItemChange(material.product_name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ material.product_name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredItemList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Item tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showItemPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <!-- Daftar Ops yang Dipilih -->
                        <v-layout row wrap v-if="selectedItemList.length > 0" class="selected-material-list">
                            <v-flex xs12 v-for="(material, index) in selectedItemList" :key="index"
                                class="selected-material-item">
                                <v-card elevation="2" class="pa-3 mb-2">
                                    <p class="font-weight-bold">{{ material.product_name }}</p>
                                    <v-text-field v-model="material.qty" label="Qty" type="number" dense outlined
                                        :min="1" :max="material.qty_available" @input="handleQtyChange(index)"
                                        oninput="this.value = (parseFloat(this.max) < parseFloat(this.value) ? this.max : this.value)"></v-text-field>
                                    <v-btn icon small @click="handleRemoveItem(index)">
                                        <v-icon color="red">mdi-delete</v-icon>
                                    </v-btn>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Nama Toko -->
                    <div class="section">
                        <v-btn @click="showStorePicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> Toko
                        </v-btn>

                        <v-dialog v-model="showStorePicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Toko</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="storeSearch" placeholder="Cari Toko..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(store, index) in filteredStoreList" :key="index"
                                            @click="handleStoreChange(store.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ store.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredStoreList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Toko tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showStorePicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedStoreList.length > 0" class="selected-store-list">
                            <v-flex xs12 v-for="(store, index) in selectedStoreList" :key="index">
                                <v-chip color="indigo lighten-2" text-color="white">
                                    {{ store.name }}
                                    <v-icon right @click="handleRemoveStore(index)"
                                        style="color:aliceblue;font-size: large;">mdi-close-circle</v-icon>
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Section PO -->
                    <div class="section">
                        <v-btn @click="showPoPicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> DO (PO)
                        </v-btn>

                        <v-dialog v-model="showPoPicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih DO (PO)</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="poSearch" placeholder="Cari DO..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(po, index) in filteredPoList" :key="index"
                                            @click="handlePoChange(po.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ po.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredPoList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>DO tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showPoPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedPoList.length > 0" class="selected-po-list">
                            <v-flex xs12 v-for="(po, index) in selectedPoList" :key="index">
                                <v-chip color="indigo lighten-2" text-color="white">
                                    {{ po.name }}
                                    <v-icon right @click="handleRemovePo(index)"
                                        style="color:aliceblue;font-size: large;">mdi-close-circle</v-icon>
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Section Complain -->
                    <div class="section">
                        <v-btn @click="showComplainPicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> Complain
                        </v-btn>

                        <v-dialog v-model="showComplainPicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Complain</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="complainSearch" placeholder="Cari Complain..."
                                        solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(complain, index) in filteredComplainList" :key="index"
                                            @click="handleComplainChange(complain.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ complain.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredComplainList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Complain tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showComplainPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedComplainList.length > 0" class="selected-complain-list">
                            <v-flex xs12 v-for="(complain, index) in selectedComplainList" :key="index">
                                <v-chip color="indigo lighten-2" text-color="white">
                                    {{ complain.name }}
                                    <v-icon right @click="handleRemoveComplain(index)"
                                        style="color:aliceblue;font-size: large;">mdi-close-circle</v-icon>
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>



                    <!-- Note -->
                    <v-textarea v-model="note" rows="3" label="Catatan" outlined dense></v-textarea>



                        <!-- Action Buttons for Deleting and Updating -->
                    <v-layout row wrap align-center justify-space-between class="mt-4">
                        <!-- Delete Button on the Left -->
                        <v-btn @click="handleDelete" color="red" class="btn-delete" large>
                            Hapus
                        </v-btn>

                        <!-- Update Button on the Right -->
                        <v-btn @click="handleSubmit" color="success" class="btn-update" large
                            style="background-color: blue; color: aliceblue;">
                            Perbarui
                        </v-btn>
                    </v-layout>


                    <p v-if="error" class="error-text mt-2">{{ error }}</p>
                </v-card-text>
            </v-card>
        </v-container>
        <v-container v-if="loading" class="d-flex justify-center align-center">
            <v-progress-circular indeterminate color="primary" size="70"></v-progress-circular>
        </v-container>
    </div>
</template>

<script>
import axios from "@/utils/axios";


export default {
    props: ["selectedMaterial", "onSubmitCompleted"],
    data() {
        return {
            allMaterialList: [],
            materialList: [],
            selectedItemList: this.selectedMaterial?.lines?.map(v => { return { ...v, unit_id: v?.unit?.id, unit: v.unit.short_name, product_name: `${v.variation.product.name} ${v.variation.product_variation.name == 'DUMMY' ? '' : v.variation.product_variation.name} ${v.variation.name == 'DUMMY' ? '' : v.variation.name}` } }) || [],
            showItemPicker: false,
            storeList: [],
            selectedStoreList: this.selectedMaterial?.any_store?.map(v => { return { id: v.id, name: `${v.store_code} ${v.store_name}` } }) || [],
            showStorePicker: false,
            poList: [],
            selectedPoList: this.selectedMaterial?.any_po?.map(v => { return { id: v.id, name: `${v?.store?.store_code} ${v?.store?.store_name}` } }) || [],
            showPoPicker: false,
            complainList: [],
            selectedComplainList: this.selectedMaterial?.any_complain?.map(v => { return { id: v.id, name: `${v?.store?.store_code} ${v?.store?.store_name}` } }) || [],
            showComplainPicker: false,
            location: {},
            selectedLocation: this.selectedMaterial?.location_id || "",
            note: "",
            error: "",
            itemSearch: "",
            storeSearch: "",
            poSearch: "",
            complainSearch: "",
            currentIndex: null,
            loading: false,
        };
    },
    computed: {
        filteredItemList() {
            return this.materialList.filter((material) =>
                material?.product_name?.toLowerCase()?.includes(this.itemSearch.toLowerCase())
            );
        },
        filteredStoreList() {
            return this.storeList.filter((store) =>
                store?.name?.toLowerCase()?.includes(this.storeSearch.toLowerCase())
            );
        },
        filteredPoList() {
            return this.poList.filter((po) =>
                po.name.toLowerCase().includes(this.poSearch.toLowerCase())
            );
        },
        filteredComplainList() {
            return this.complainList.filter((complain) =>
                complain.name.toLowerCase().includes(this.complainSearch.toLowerCase())
            );
        },
        maxValue() {
            return this
        }
    },
    methods: {

        handleLocationChange(itemValue) {
            this.selectedLocation = itemValue;
            this.filterMaterialsByLocation()
        },
        filterMaterialsByLocation() {

            this.materialList = this.allMaterialList.filter(
                (material) => material.location_id == this.selectedLocation
            );

        },
        handleItemChange(itemValue) {
            if (!this.selectedItemList.some((item) => item.name === itemValue)) {
                const selectedItemObject = this.materialList.find(
                    (material) => material.product_name === itemValue
                );

                this.selectedItemList.push({
                    ...selectedItemObject,
                    qty: 1,
                });
            }
            this.showItemPicker = false;
            this.itemSearch = "";
        },
        handleStoreChange(itemValue) {
            // Pastikan `itemValue` tidak null dan tidak ada item dengan nama yang sama di `selectedStoreList`
            if (itemValue && !this.selectedStoreList.some(item => item.name === itemValue)) {
                const selectedStoreObject = this.storeList.find(store => store.name === itemValue);
                if (selectedStoreObject) {
                    // Tambahkan item yang dipilih ke daftar `selectedStoreList`
                    this.selectedStoreList.push({ ...selectedStoreObject });
                }
            }
            // Tutup picker dan kosongkan pencarian
            this.showStorePicker = false;
            this.storeSearch = '';
        },
        handleQtyChange(index) {
            const material = this.selectedItemList[index]; console.log(material)
            if (material.qty > material.qty_available) {
                material.qty = material.qty_available; // Batasi qty ke nilai maksimum
            } else if (material.qty < 1) {
                material.qty = 1; // Batasi qty ke nilai minimum
            }
            if (material.qty % 1 === 0) {
                // Jika iya, ubah menjadi integer
                material.qty = Math.floor(material.qty); // Anda bisa juga menggunakan Math.round() jika dibutuhkan
            }
        },
        handlePoChange(itemValue) {
            if (!this.selectedPoList.some((item) => item.name === itemValue)) {
                const selectedPoObject = this.poList.find(po => po.name === itemValue);
                this.selectedPoList.push(selectedPoObject);
            }
            this.showPoPicker = false;
            this.poSearch = "";
        },
        handleRemovePo(index) {
            this.selectedPoList.splice(index, 1);
        },
        handleComplainChange(itemValue) {
            if (!this.selectedComplainList.some((item) => item.name === itemValue)) {
                const selectedComplainObject = this.complainList.find(complain => complain.name === itemValue);
                this.selectedComplainList.push(selectedComplainObject);
            }
            this.showComplainPicker = false;
            this.complainSearch = "";
        },
        handleRemoveComplain(index) {
            this.selectedComplainList.splice(index, 1);
        },
        handleRemoveItem(index) {
            this.selectedItemList.splice(index, 1);
        },
        handleRemoveStore(index) {
            this.selectedStoreList.splice(index, 1);
        },
        handleDelete() {
            // Confirmation dialog before deletion
            if (confirm("Apakah Anda yakin ingin menghapus Pengajuan ini?")) {
                this.loading = true; // Show loading indicator
                axios
                    .delete(`/surat-jalan/${this.selectedMaterial?.id}`)
                    .then(response => {
                        console.log("Data berhasil dihapus", response.data);
                        this.onSubmitCompleted({
                            ...response.data,
                            remove: this.selectedMaterial?.id
                        }); // Callback after successful deletion
                    })
                    .catch(error => console.error("Error deleting data:", error))
                    .finally(() => {
                        this.loading = false
                    });
            }
        },
        async handleSubmit() {

            if (this.selectedItemList.length === 0) {
                this.error = 'Harap tambahkan minimal satu nama Ops';
                return;
            }
            // if (this.selectedItemList.some(v => v.nominal < 1000)) {
            //   this.error = 'Nominal tidak boleh kurang dari 1000';
            //   return;
            // }

            this.error = '';

            const formData = new FormData();
            formData.append('location_id', this.selectedLocation);
            this.selectedItemList.forEach((v, i) => {
                formData.append(`products[${i}][variation_id]`, v.variation_id);
                formData.append(`products[${i}][unit_id]`, v.unit_id);
                formData.append(`products[${i}][qty]`, v.qty);
            })


            formData.append('stores', this.selectedStoreList.map(v => v.id));
            formData.append('pos', this.selectedPoList.map(v => v.id));
            formData.append('complains', this.selectedComplainList.map(v => v.id));

            formData.append('sj_note', this.note);





            try {
                this.loading = true;
                const { data } = await axios.post('/surat-jalan/post-sj-form-data', formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                if (data?.success) {
                    this.$toast?.success(data?.msg || 'Data berhasil ditambahkan')
                    setTimeout(() => {
                        this.$router.push({
                            name: 'Material',
                        });
                    }, 2e3);
                } else {
                    this.$toast?.error(data?.msg || 'Ada kesalahan cek, console.warn')
                    console.log(data)
                }
            } catch (error) {
                console.error(error);
            } finally {
                this.loading = false;
            }
        },
        async fetchParamsFromServer() {
            try {
                const response = await axios.get('/material/getSJFormData');
                this.initializeData(response.data);
            } catch (error) {
                console.error("Gagal mengambil data dari server:", error);
            }
        },
        initializeData(data) {
            // Assign nilai `data` ke variabel lokal di komponen
            this.allMaterialList = data.products || [];

            this.storeList = data.stores || [];
            this.poList = data.poList || [];
            this.complainList = data.complains || [];
            this.location = data.locations || {};

            this.selectedLocation = this.selectedMaterial?.location_id?.toString() || Object.keys(data.locations || [])[0] || '';
            
            this.filterMaterialsByLocation()
        },
    },

    async created() {
        await this.fetchParamsFromServer();
    },

    async mounted() {



    }
};
</script>

<style scoped>
.operasional-form {
    max-width: 800px;
    margin: auto;
}

.section {
    margin-bottom: 20px;
}

.result-item:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}

.selected-material-list,
.selected-store-list {
    margin-top: 15px;
}

/* Mengatur gaya untuk tombol outlined */
.btn-custom {
    background-color: white !important;
    border: 1px solid #1976d2 !important;
    /* Warna outline */
    color: #1976d2 !important;
    /* Warna teks dan ikon */
    transition: background-color 0.3s ease, color 0.3s ease;
}

.btn-custom:hover {
    background-color: #1976d2 !important;
    /* Warna latar belakang saat hover */
    color: white !important;
    /* Warna teks dan ikon saat hover */
}

.result-item {
    display: flex;
    align-items: center;
    padding: 8px;
    margin-bottom: 4px;
    border-bottom: 1px solid #e0e0e0;
    /* Membuat garis pemisah */
}

.result-item:last-child {
    border-bottom: none;
    /* Menghilangkan garis pada item terakhir */
}


.total {
    font-weight: bold;
    color: #4caf50;
}

.btn-submit {
    font-weight: bold;
    font-size: 18px;
}

.files-container {
    display: flex;
    flex-wrap: wrap;
}

.file-item {
    position: relative;
}

.file-thumbnail {
    width: 80px;
    height: 80px;
    object-fit: cover;
}

.file-image {
    border-radius: 8px;
    height: 80px;
    object-fit: cover;
    border: 2px solid #eee;
}

.error-text {
    color: red;
    font-weight: bold;
}
</style>