<template>
    <v-card class="material-show-screen">
      <v-card-title class="title">
        Detail PO Material
      </v-card-title>
  
      <v-divider></v-divider>
  
      <v-card-text>
        <!-- General Information -->
        <section class="section">
          <h3 class="section-title">Informasi Umum</h3>
          <div class="info-row">
            <span class="label">Reference No:</span>
            <span class="value">{{ selectedMaterial.ref_no }}</span>
          </div>
          <div class="info-row">
            <span class="label">Tanggal SJ:</span>
            <span class="value">{{ formatDate(selectedMaterial.sj_date) }}</span>
          </div>
          <div class="info-row">
            <span class="label">Status:</span>
            <v-chip :class="statusClass" small>{{ selectedMaterial.status | capitalize }}</v-chip>
          </div>
          <div class="info-row">
            <span class="label">Catatan:</span>
            <span class="value">{{ selectedMaterial.sj_note || 'Tidak ada catatan' }}</span>
          </div>
        </section>
  
        <v-divider></v-divider>
  
        <!-- Personnel Information -->
        <section class="section">
          <h3 class="section-title">Team Works</h3>
          <div class="info-row">
            <span class="label">Teknisi:</span>
            <span class="value">{{ getFullName(selectedMaterial.teknisi) }}</span>
          </div>
          <div class="info-row">
            <span class="label">Helper:</span>
            <span class="value">{{ getFullName(selectedMaterial.helper) }}</span>
          </div>
          <div class="info-row">
            <span class="label">Driver:</span>
            <span class="value">{{ getFullName(selectedMaterial.driver) }}</span>
          </div>
        </section>
  
        <v-divider></v-divider>
  
        <!-- Location and Store Information -->
        <section class="section">
          <h3 class="section-title">Lokasi & Toko</h3>
          <div class="info-row">
            <span class="label">Lokasi:</span>
            <span class="value">{{ selectedMaterial.location.name }}</span>
          </div>
          <div class="info-row" v-if="selectedMaterial.any_complain && selectedMaterial.any_complain.length">
            <span class="label">Toko:</span>
            <span class="value">
              {{ selectedMaterial.any_complain.map(v => `${v.store?.store_code} ${v.store?.store_name}`).join(', ') }}
            </span>
          </div>
          <div class="info-row" v-if="selectedMaterial.any_store && selectedMaterial.any_store.length">
            <span class="label">Toko:</span>
            <span class="value">
              {{ selectedMaterial.any_store.map(v => `${v.store_code} ${v.store_name}`).join(', ') }}
            </span>
          </div>
          <div class="info-row" v-if="selectedMaterial.any_po && selectedMaterial.any_po.length">
            <span class="label">Toko:</span>
            <span class="value">
              {{ selectedMaterial.any_po.map(v => `${v.store?.store_code} ${v.store?.store_name}`).join(', ') }}
            </span>
          </div>
        </section>
  
        <v-divider></v-divider>
  
        <!-- Items List -->
        <section class="section" v-if="selectedMaterial.lines && selectedMaterial.lines.length">
          <h3 class="section-title">Daftar Item</h3>
          <v-list dense>
            <v-list-tile v-for="line in selectedMaterial.lines" :key="line.id" class="list-item">
              <v-list-tile-avatar>
                <img :src="line.variation.product.image_url || defaultImage" />
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ line.variation.product.name }}
                  <span v-if="line.variation.product_variation.name !== 'DUMMY'" class="variation-name">
                    - {{ line.variation.product_variation.name }}
                  </span>
                </v-list-tile-title>
                <v-list-tile-sub-title>
                  {{ parseInt(line.qty) }} {{ line.unit.short_name }}
                </v-list-tile-sub-title>
              </v-list-tile-content>
              <v-divider></v-divider>
            </v-list-tile>
          </v-list>
        </section>
      </v-card-text>
  
      <v-divider></v-divider>
  
      <v-card-actions>
        <v-btn @click="$emit('close')" style="color: white;background-color: red;">Tutup</v-btn>
      </v-card-actions>
    </v-card>
  </template>
  
  

<script>
export default {
    name: "MaterialShowScreen",
    props: {
        selectedMaterial: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            defaultImage: "https://ac.igm.biz.id/img/default.png" // Default image URL
        };
    },
    computed: {
        statusClass() {
            switch (this.selectedMaterial.status) {
                case 'completed':
                    return 'status-completed'; // Green
                case 'pending':
                    return 'status-pending'; // Orange
                case 'in-progress':
                    return 'status-in-progress'; // Cyan
                default:
                    return '';
            }
        }
    },
    methods: {
        getFullName(person) {
            if(!person) return '';
            return `${person.first_name || ''} ${person.surname || ''} ${person.last_name || ''}`.trim();
        },
        formatDate(dateString) {
            const options = { year: "numeric", month: "short", day: "numeric" };
            return new Date(dateString).toLocaleDateString(undefined, options);
        }
    },
    filters: {
        capitalize(value) {
            if (!value) return '';
            return value.charAt(0).toUpperCase() + value.slice(1);
        }
    }
};
</script>

<style scoped>
.material-show-screen {
    max-width: 600px;
}

.title {
    font-size: 1.25rem;
    font-weight: bold;
}

.section {
    margin-top: 10px;
}

.section-title {
    font-size: 1.1rem;
    font-weight: bold;
    color: #1976d2;
    margin-bottom: 10px;
}

.list-item {
    padding: 16px 0;
}


.variation-name {
    color: #555;
    /* Secondary color for variation name */
    font-style: italic;
    /* Italicize variation names */
}
.v-divider {
  margin: 12px 0; /* Add margin to the divider to increase spacing */
}

.info-row {
    display: flex;
    justify-content: space-between;
    padding: 4px 0;
    font-size: 0.9rem;
}

.label {
    font-weight: bold;
    color: #555;
}

.value {
    text-align: right;
    color: #333;
}

.v-list-item-avatar {
    background-color: #f0f0f0;
}

.status-completed {
    background-color: #4caf50;
    /* Green */
    color: white;
}

.status-pending {
    background-color: #ff9800;
    /* Orange */
    color: white;
}

.status-in-progress {
    background-color: #00bcd4;
    /* Cyan */
    color: white;
}
</style>