<template>
    <v-card class="material-item" @click="$emit('click', material)">
      <v-card-text>
        <div class="info-row">
          <span class="label">Ref No:</span>
          <span class="value">{{ material.ref_no }}</span>
        </div>
        <div class="info-row">
          <span class="label">Tanggal :</span>
          <span class="value">{{ formatDate(material.sj_date) }}</span>
        </div>
        <div class="info-row">
          <span class="label">Teknisi:</span>
          <span class="value">{{ getFullName(material.teknisi) }}</span>
        </div>
        <div class="info-row">
          <span class="label">Status:</span>
          <span class="value">{{ material.status }}</span>
        </div>
        <div v-if="material.sj_note" class="info-row">
          <span class="label">Catatan:</span>
          <span class="value">{{ material.sj_note }}</span>
        </div>
      </v-card-text>
  
      
    </v-card>
  </template>
  
  <script>
  export default {
    name: "MaterialItem",
    props: {
      material: {
        type: Object,
        required: true
      }
    },
    methods: {
      getFullName(person) {
        if(!person) return '';
        return `${person.first_name || ''} ${person.surname || ''} ${person.last_name || ''}`.trim();
      },
      editMaterial() {
        this.$emit("edit", this.material);
      },
      deleteMaterial() {
        this.$emit("delete", this.material);
      },
      formatDate(dateString) {
        const options = { year: "numeric", month: "short", day: "numeric" };
        return new Date(dateString).toLocaleDateString(undefined, options);
      }
    }
  };
  </script>
  
  <style scoped>
  .material-item {
    margin: 10px;
  }
  
  .info-row {
    display: flex;
    justify-content: space-between;
    padding: 4px 0;
    font-size: 0.9rem;
  }
  
  .label {
    font-weight: bold;
    text-align: left;
  }
  
  .value {
    text-align: right;
  }
  </style>
  