<template>
    <div>
        <div class="header-container">
            <h2>Daftar PO Material</h2>
            <v-btn icon  @click="goToMaterialForm" color="blue">
                <v-icon>mdi-plus-circle</v-icon>
            </v-btn>
        </div>

        <v-container>
            <v-layout row wrap>
                <v-flex v-for="(item, index) in materialData" :key="index" xs12 md4>
                    <MaterialItem :material="item" @click.native="handleItemPress(item)" />
                </v-flex>
            </v-layout>

            <div v-if="!materialData.length" class="no-data-text">Tidak ada PO Material</div>
        </v-container>

        <!-- Conditional Dialogs -->
        <v-dialog v-model="dialogVisible" max-width="600">
            <component :is="selectedMaterial?.status != 'pending' ? 'MaterialShowScreen' : 'MaterialEditForm'"
                :selectedMaterial="selectedMaterial" :paramsFromServer="paramsFromServer" @close="closeDialog"
                :onSubmitCompleted="onSubmitCompleted" v-if="selectedMaterial" />
        </v-dialog>

        

        <v-progress-circular v-if="loading" indeterminate color="primary" />
    </div>
</template>


<script>
import axios from "@/utils/axios";
import MaterialItem from './MaterialItem';
// import MaterialForm from './MaterialForm';
import MaterialEditForm from './MaterialEditForm';
import MaterialShowScreen from './MaterialShowScreen';

export default {
    components: {
        MaterialItem,
        // MaterialForm,
        MaterialEditForm,
        MaterialShowScreen,
    },
    data() {
        return {
            materialData: [],
            loading: false,
            formVisible: false,  // Set to false initially
            dialogVisible: false,
            selectedMaterial: null,
            paramsFromServer: {},
        };
    },
    methods: {
        async fetchMaterial() {
            this.loading = true;
            try {
                const response = await axios.get('/surat-jalan');
                this.materialData = response.data;
            } catch (error) {
                console.error("Error fetching materials:", error);
            } finally {
                this.loading = false;
            }
        },
        goToMaterialForm() {
            this.$router.push({ name: 'MaterialForm' });
        },
        async fetchFormMaterial() {
            try {
                const response = await axios.get('/material/getSJFormData');
                this.paramsFromServer = response.data;
            } catch (error) {
                console.error("Error fetching form data:", error);
            }
        },
        onSubmitCompleted({ success, msg, remove }) {
            this.formVisible = false; // Close form after submission
            if (success) {
                this.fetchMaterial();
                this.$toast.success(msg || 'Data berhasil disimpan.');
            } else {
                this.$toast.error(msg || 'Terjadi kesalahan. Silakan coba lagi.');
            }
            this.closeDialog();

            if (remove) {
                this.materialData = this.materialData.filter(item => item.id !== remove);
            }
        },
        
        handleItemPress(material) {
            this.selectedMaterial = material;
            this.dialogVisible = true; // Open detail/edit dialog
        },
        closeDialog() {
            this.dialogVisible = false;
            this.selectedMaterial = null;
        },
    },
    created() {
        this.fetchMaterial(); // Fetch materials on component creation
    },
};
</script>

<style scoped>
.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.no-data-text {
    text-align: center;
    margin-top: 20px;
}
</style>